import React, { useState } from 'react';
import About from './About';
import Resume from './Resume';
import Portfolio from './Portfolio';
import cn from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faTwitter,
  faGithub,
  faLinkedinIn,
} from '@fortawesome/free-brands-svg-icons';

const RightSide = () => {
  const [tab, setTab] = useState('ABOUT');
  const tabs = {
    ABOUT: 'ABOUT',
    RESUME: 'RESUME',
    PORTFOLIO: 'PORTFOLIO',
  };
  const handleClick = (tabName) => {
    setTab(tabName);
  };
  const aboutClass = cn('Right-nav__item', {
    active: tab === tabs.ABOUT,
  });
  const resumeClass = cn('Right-nav__item', {
    active: tab === tabs.RESUME,
  });
  const portfolioClass = cn('Right-nav__item', {
    active: tab === tabs.PORTFOLIO,
  });
  return (
    <section className="Right">
      <nav className="Right-nav">
        <div className={aboutClass} onClick={() => handleClick(tabs.ABOUT)}>
          About
        </div>
        <div className={resumeClass} onClick={() => handleClick(tabs.RESUME)}>
          Resume
        </div>
        <div
          className={portfolioClass}
          onClick={() => handleClick(tabs.PORTFOLIO)}
        >
          Portfolio
        </div>
      </nav>
      <div className="Right-tab">
        {tab === tabs.ABOUT && <About />}
        {tab === tabs.RESUME && <Resume />}
        {tab === tabs.PORTFOLIO && <Portfolio />}
      </div>
      <div className="Right-media">
        <a href="https://twitter.com/mhasanince">
          <FontAwesomeIcon
            icon={faTwitter}
            size="2x"
            className="Right-media__twitter icon"
          />
        </a>
        <a href="https://github.com/mhasanince">
          <FontAwesomeIcon
            icon={faGithub}
            size="2x"
            className="Right-media__github icon"
          />
        </a>
        <a href="https://www.linkedin.com/in/mhasanince/">
          <FontAwesomeIcon
            icon={faLinkedinIn}
            size="2x"
            className="Right-media__linkedin icon"
          />
        </a>
      </div>
    </section>
  );
};

export default RightSide;
